<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.user_mobile" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="" v-model="filters.s_time" style="width: 100%;"></el-date-picker>
                    </el-col>
                    <el-col class="line" :span="2"> - </el-col>
                    <el-col :span="11">
                        <el-time-picker type="date" placeholder="" v-model="filters.e_time" style="width: 100%;"></el-time-picker>
                    </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getUsers"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" ></el-button>
                </el-form-item>
            </el-form>
        </el-col>
    
        <el-col :span="24" class="toolbar">
            <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
                <el-tab-pane label="" name="10" ></el-tab-pane>
                <el-tab-pane label="" name="0" ></el-tab-pane>
                <el-tab-pane label="" name="1" ></el-tab-pane>
                <el-tab-pane label="" name="2" ></el-tab-pane>
                <el-tab-pane label="" name="3" ></el-tab-pane>
            </el-tabs>
        </el-col>
        <!---->
        <template>
            <el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
                <el-table-column type="index" width="60">
                </el-table-column>
                <el-table-column prop="user_mobile" label="" width="180" sortable>
                </el-table-column>
                <el-table-column prop="card_tip" label="" width="100" sortable>
                </el-table-column>
                <el-table-column prop="card_num" label="" width="180" sortable>
                </el-table-column>
                <el-table-column prop="status" label="" width="120" :formatter="formatSex" sortable>
                </el-table-column>
                <el-table-column prop="money" label="" width="150" sortable>
                </el-table-column>
                <el-table-column prop="created_at" label="" width="150" sortable>
                </el-table-column>
                <el-table-column inline-template :context="_self" label="" min-width="200">
                    <span>
                        <el-button size="small" @click="audit"></el-button>
                        <el-button size="small" @click="audit"></el-button>
                        <el-button size="small" @click="audit"></el-button>
                        <!--<el-button size="small" @click="handleEdit(row)"></el-button>-->
                    </span>
                </el-table-column>
            </el-table>
        </template>
    
        <!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination layout="total,sizes,prev, pager, next" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[10, 200, 300, 400]" :page-size="pagesize" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
    
        <!---->
        <el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-switch on-text="" off-text="" v-model="editForm.delivery"></el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-switch on-text="" off-text="" v-model="editForm.delivery"></el-switch>
                </el-form-item>
                <el-form-item label="" prop="ceshi">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="editForm.addr"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"> </el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
import util from '../../common/util'
import NProgress from 'nprogress'
import request from 'api';
import config from 'config';

export default {
    data() {
        return {
            activeName:'10',
            filters: {
                name: '',
                dates: '',
                datee: '',
                status:'10'
            },
            users: [],
            total: 0,
            page: 1,
            pagesize:10,
            listLoading: false,
            editFormVisible: false,//
            editFormTtile: '',//
            //
            editForm: {
                id: 0,
                name: '',
                sex: -1,
                age: 0,
                birth: '',
                addr: ''
            },
            editLoading: false,
            btnEditText: ' ',
            editFormRules: {
                name: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            }

        }
    },
    methods: {
        //
        formatSex(row, column) {
            switch(row.status){
                case 0:
                    return '';
                case 1:
                    return '';
                case 2:
                    return '';
                case 3:
                    return '';
                
            }
            
        },
        handleClick(tab,event){            
            this.filters.status=tab.name;
            this.getUsers();
        },
        audit() {

        },
        handleCurrentChange(val) {
            this.page = val;
            this.getUsers();
        },
        handleSizeChange(val) {
			console.log(` ${val} `);
		},
        //
        getUsers() {
            let para = {
                page: this.page,
                name: this.filters.name,
                status:this.filters.status
            };
            this.listLoading = true;
            NProgress.start();

            request.get(config.api.fund.showlist,para)
				.then((res) => {
					this.listLoading = false;
					NProgress.done();
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.total = data.cnt.total;
						this.users = data.cnt.data;
						this.pagesize = data.cnt.per_page || 10;
					}
				})
        },
        //
        handleDel: function (row) {
            //console.log(row);
            var _this = this;
            this.$confirm('?', '', {
                //type: 'warning'
            }).then(() => {
                _this.listLoading = true;
                NProgress.start();
                let para = { id: row.id };
                removeUser(para).then((res) => {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                    });
                    _this.getUsers();
                });

            }).catch(() => {

            });
        },
        //
        handleEdit: function (row) {
            this.editFormVisible = true;
            this.editFormTtile = '';
            this.editForm.id = row.id;
            this.editForm.name = row.name;
            this.editForm.sex = row.sex;
            this.editForm.age = row.age;
            this.editForm.birth = row.birth;
            this.editForm.addr = row.addr;
        },
        // or 
        editSubmit: function () {
            var _this = this;

            _this.$refs.editForm.validate((valid) => {
                if (valid) {

                    _this.$confirm('', '', {}).then(() => {
                        _this.editLoading = true;
                        NProgress.start();
                        _this.btnEditText = '';

                        if (_this.editForm.id == 0) {
                            //
                            let para = {
                                name: _this.editForm.name,
                                sex: _this.editForm.sex,
                                age: _this.editForm.age,
                                birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
                                addr: _this.editForm.addr,
                            };
                            addUser(para).then((res) => {
                                _this.editLoading = false;
                                NProgress.done();
                                _this.btnEditText = ' ';
                                _this.$notify({
                                    title: '',
                                    message: '',
                                    type: 'success'
                                });
                                _this.editFormVisible = false;
                                _this.getUsers();
                            });
                        } else {
                            //
                            let para = {
                                id: _this.editForm.id,
                                name: _this.editForm.name,
                                sex: _this.editForm.sex,
                                age: _this.editForm.age,
                                birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
                                addr: _this.editForm.addr,
                            };
                            editUser(para).then((res) => {
                                _this.editLoading = false;
                                NProgress.done();
                                _this.btnEditText = ' ';
                                _this.$notify({
                                    title: '',
                                    message: '',
                                    type: 'success'
                                });
                                _this.editFormVisible = false;
                                _this.getUsers();
                            });

                        }

                    });

                }
            });

        },
        //
        handleAdd: function () {
            var _this = this;

            this.editFormVisible = true;
            this.editFormTtile = '';

            this.editForm.id = 0;
            this.editForm.name = '';
            this.editForm.sex = 1;
            this.editForm.age = 0;
            this.editForm.birth = '';
            this.editForm.addr = '';
        }
    },
    mounted() {
        this.getUsers();
    }
}
</script>

<style scoped>

</style>


